@import url('https://fonts.googleapis.com/css?family=Lato:300,400|Poppins:300,400,800&display=swap');

//VERTICAL LINE
.vl{
    position: absolute;
    height: 80px;
    left: 49.9%;
    top: 97%;
    border-left: 2px solid #ffeb3b;
}
//HORIZONTAL LINE
.hl{
    margin-top: 20px;
    width: 180px;
    margin-left: 70px;
    border: 1px solid #c786d3;
}
.headers{
    font-family: 'Lato';
    color: black;
    font-size: 25px;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 5px;
}

.aboutMsg{
    font-family: 'Poppins';
    font-weight: 400;
    color: black;
    font-size: 16px;
    text-align: center;
    letter-spacing: 0px;
}